import { useState } from 'react';
import mygif from './1.gif';

function Note() {

  const [hide, setHide] = useState(true);

  const x = hide ? 'flex' : 'none';

  return (
    <div className="note" style={{display: x}}>
        
        <div className="note-information">
          <h2>NOTA:</h2>
          <p>Aplikasaun ida nee kria husi HTML, CSS, JavaScript no Library ReactJS. Aplikasaun ida nee ho inisiativa atu dezenvolve diak liu tan ita nia koniesimentu iha Zona Timor-Leste.</p>
          <img src={mygif} alt={mygif} />
          <p className="close" onClick={
            () => setHide(state => !state)
          }>X</p>
        </div>

      </div>
  );
}

function Welcome() {

  return(
    <div>
      <h1 className="welcome-heading">Bemvindu Mai iha Hau nia App.</h1>

      <Note />
    </div>
  )
}

export default Welcome;